import { runExport } from 'api/exports/exportUtils';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchExportRequest, success: fetchExportSuccess, failure: fetchExportFailure, } = createAsyncAction('export/request', 'export/success', 'export/failure')();
export function exportReport(reportPathsType, reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchExportRequest());
        runExport(reportPathsType, reportType, query)
            .then(res => {
            dispatch(fetchExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchExportFailure(err));
        });
    };
}
