import { runExport } from 'api/exports/ocpCloudExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchOcpCloudExportRequest, success: fetchOcpCloudExportSuccess, failure: fetchOcpCloudExportFailure, } = createAsyncAction('ocpCloudExport/request', 'ocpCloudExport/success', 'ocpCloudExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchOcpCloudExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchOcpCloudExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchOcpCloudExportFailure(err));
        });
    };
}
