import fileDownload from 'js-file-download';
import { getType } from 'typesafe-actions';
import { fetchExportFailure, fetchExportRequest, fetchExportSuccess, } from './exportActions';
export const defaultState = {
    export: null,
    exportError: null,
    exportFetchStatus: 0 /* none */,
};
export const stateKey = 'awsExport';
export function exportReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchExportRequest):
            return Object.assign({}, state, { exportFetchStatus: 1 /* inProgress */ });
        case getType(fetchExportSuccess):
            fileDownload(action.payload, 'report.csv', 'text/csv');
            return Object.assign({}, state, { export: action.payload, exportError: null, exportFetchStatus: 2 /* complete */ });
        case getType(fetchExportFailure):
            return Object.assign({}, state, { export: null, exportError: action.payload, exportFetchStatus: 2 /* complete */ });
        default:
            return state;
    }
}
