import { runReport } from 'api/reports/ocpCloudReports';
import { createStandardAction } from 'typesafe-actions';
import { getReportId } from './ocpCloudReportsCommon';
import { selectReport, selectReportFetchStatus, } from './ocpCloudReportsSelectors';
const expirationMS = 30 * 60 * 1000; // 30 minutes
export const fetchOcpCloudReportRequest = createStandardAction('ocpCloudReports/request')();
export const fetchOcpCloudReportSuccess = createStandardAction('ocpCloudReports/success')();
export const fetchOcpCloudReportFailure = createStandardAction('ocpCloudReports/failure')();
export function fetchReport(reportType, query) {
    return (dispatch, getState) => {
        if (!isReportExpired(getState(), reportType, query)) {
            return;
        }
        const meta = {
            reportId: getReportId(reportType, query),
        };
        dispatch(fetchOcpCloudReportRequest(meta));
        runReport(reportType, query)
            .then(res => {
            // See https://github.com/project-koku/koku-ui/pull/580
            // const repsonseData = dropCurrentMonthData(res, query);
            dispatch(fetchOcpCloudReportSuccess(res.data, meta));
        })
            .catch(err => {
            dispatch(fetchOcpCloudReportFailure(err, meta));
        });
    };
}
function isReportExpired(state, reportType, query) {
    const report = selectReport(state, reportType, query);
    const fetchStatus = selectReportFetchStatus(state, reportType, query);
    if (fetchStatus === 1 /* inProgress */) {
        return false;
    }
    if (!report) {
        return true;
    }
    const now = Date.now();
    return now > report.timeRequested + expirationMS;
}
