import { getRBAC } from 'api/rbac';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchRbacRequest, success: fetchRbacSuccess, failure: fetchRbacFailure, } = createAsyncAction('fetch/RBAC/request', 'fetch/RBAC/success', 'fetch/RBAC/failure')();
export const fetchRbac = () => {
    return (dispatch) => {
        dispatch(fetchRbacRequest());
        return getRBAC()
            .then(res => {
            dispatch(fetchRbacSuccess(res));
        })
            .catch(err => {
            dispatch(fetchRbacFailure(err));
        });
    };
};
